export const getReportHtml= url => {
  // return axios.get(url, {
  //   headers: config.header()
  // })
  var xhr = new XMLHttpRequest();
  xhr.onloadend = function(){
    console.log(xhr);
  };
  xhr.open('GET','http://institute.cedartest.com/reportv2/selectsubject/peasonreport/gread-9-class-1-testid-763-sid-100544-subject-01-id-2669-print-1.shtml');
  xhr.setRequestHeader('Content-Type','html');
  xhr.send();
}


